import dayjs from 'dayjs'
import Excel from 'exceljs'
import { uniq } from 'lodash'

import { Api } from '../../data/api'
import { fetchBoxTaxaReportByBoxId } from '../../data/calls'
import { downloadWorkbook } from '../../lib/downloadWorkbook'

const TAXONOMIST_COLUMNS = { header: '', key: '' } // placeholder which is replaced with taxonomist columns

const columnConfig = [
  { header: 'Order', key: 'order' },
  { header: 'Family', key: 'family' },
  { header: 'Scientific Name', key: 'scientificName' },
  TAXONOMIST_COLUMNS,
  { header: 'Total Sample Count', key: 'sampleCount' },
  { header: 'Total Split Count', key: 'totalCount' },
]

export const downloadBoxTaxaReport = async ({
  api,
  boxId,
  filename,
}: {
  api: Api
  boxId: number
  filename: string
}): Promise<boolean> => {
  const report = await fetchBoxTaxaReportByBoxId(api)(boxId)
  if (!report) throw new Error('box not found')

  // create workbook, worksheet
  const wb = new Excel.Workbook()
  const ws = wb.addWorksheet('Box Taxonomy Report')

  // add top rows data
  ws.getRow(1).values = [`Taxonomic list and sample counts for ${report.sampleCount} samples, Box ID ${report.boxId}.`]
  ws.getRow(2).values = [`List generated on ${dayjs().format('MMMM D, YYYY')}.`]
  ws.getRow(3).values = [`Customer: ${report.customerName}.`]
  const tableHeaderRowNumber = 4

  // get taxonomist names/columns, build updated columm config
  const taxonomistNames = report.boxTaxa.reduce<string[]>((acc, record) => {
    return uniq([...acc, ...Object.keys(record.taxonomistCounts)])
  }, [])
  const updatedColumnConfig = columnConfig
  columnConfig.splice(
    columnConfig.findIndex((col) => col === TAXONOMIST_COLUMNS),
    1,
    ...taxonomistNames.map((name) => ({ header: name, key: name }))
  )

  // create row data arrays (as addTable requires), including spread taxonomist names
  const rows = report.boxTaxa
    .map((record) => ({ ...record, ...record.taxonomistCounts }))
    .map((record) => {
      const fields: (string | number)[] = []
      columnConfig.forEach(({ key }) => {
        fields.push(record[key])
      })
      return fields
    })

  // add data table
  ws.addTable({
    name: 'BoxTaxonomy',
    ref: `A${tableHeaderRowNumber}`,
    headerRow: true,
    style: {
      theme: 'TableStyleLight1',
      showRowStripes: true,
    },
    columns: updatedColumnConfig.map(({ header }) => ({ name: header, filterButton: true })),
    rows,
  })

  // autofit columns
  ws.columns.forEach((column) => {
    const columnValues = column.values
    if (!columnValues) return
    const lengths = columnValues.slice(tableHeaderRowNumber).map((value) => (value || '').toString().length)
    const maxLength = Math.max(...lengths.filter((v) => typeof v === 'number'))
    column.width = maxLength + 2 // add some padding for filter button
  })

  // freeze rows
  ws.views = [{ state: 'frozen', xSplit: undefined, ySplit: tableHeaderRowNumber, activeCell: 'A1' }]

  // download file
  downloadWorkbook(wb, filename)

  return true
}
